.Button {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid #cccccc;
    color: #000000;
    margin-right: 15px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
    cursor: pointer;

    &:active {
        box-shadow: inset 2px 2px 1px rgba(0, 0, 0, .3);
    }

    &:disabled {
        background-color: #cccccc;
        color: #000000;
        cursor: not-allowed;
    }

    &.error {
        background-color: rgba(240, 87, 108, 1);
    }

    &.success {
        background-color: rgba(161,240, 69, 1);
    }

    &.primary {
        background-color: #2884f6;
        color: #ffffff;
    }


}

